    *{
     margin: 0; 
     padding: 0;
     box-sizing: border-box;
     font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

		*::-webkit-scrollbar{
			display: none !important;
		}
		body{
			background: #222222;
			color: #fff
		}
	h2{
		margin-top: 2rem;
		text-align: center;
		font-size: 2rem;
		 font-family:Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		text-decoration: 2px red underline;
	}

    .form__group {
      position: relative;
      padding: 15px 0 0;
      margin:2rem auto;
      width: 50%;
    }

    .form__field {
      font-family: inherit;
      width: 100%;
      border: 0;
      border-bottom: 2px solid #9b9b9b;
      outline: 0;
      font-size: 1.3rem;
      color: #fff;
      padding: 7px 0;
      background: transparent;
      transition: border-color 0.2s;
    }

    .form__field::placeholder {
      color: transparent;
    }

    .form__field:placeholder-shown~.form__label {
      font-size: 1.3rem;
      cursor: text;
      top: 20px;
    }


    .form__label {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: #9b9b9b;
    }

    .form__field:focus~.form__label {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: #11998e;
      padding-bottom: 6px;
      font-weight: 700;
      border-width: 3px;
      border-image: linear-gradient(to right, #11998e, #38ef7d);
      border-image-slice: 1;
    }

    /* reset input */
    .form__field {
      box-shadow: none;
    }



		
    .coin-head-row {
			
			overflow-x: scroll; 
      padding: 10px 0;
      width: 90%;
      justify-content: center;
      display: flex;
      flex-direction: row;
      align-items: center;
      /* border-top: 1px solid rgba(78, 228, 255, 0.438); */
      border-bottom: 1px solid rgba(78, 228, 255, 0.438);
      margin: 2px;
    }
    .coin-head {
      display: flex;
      align-items: center;
      padding-right: 4px;
      justify-content: space-between;
      width: 100%;
    }

    .coin-head-rank {
      width: 5% ;
      
      text-align: center;
    }
    .coin-head-name {
      width: 25%;
      justify-content: left;
			padding-left: 5rem;
    }

    .coin-head-symbol {
      width: 10%;
      text-align: center;
    }

    .coin-head-price {
      width: 10%;
      text-align: right;
    }

    .coin-head-volume {
      width: 20%;
      text-align: right;
    }

    .coin-head-percent {
      width: 10%;
      text-align: right;
    }

    .coin-head-marketcap {
      text-align: right;
      width: 20%;
    }